import React from "react";
import {hashHistory} from "react-router";
import MyAjax from "./MyAjax.js";
import "../scss/find.scss";
export default class Forget extends React.Component{
	constructor(props){
		super(props)
	}
	componentWillMount(){
		
	}
	toback(){
		window.history.go(-1)
	}
	render(){
		
		
		return(
			<div className="type">
				<header id="for_Header">
					<span className="iconfont" onClick={this.toback.bind(this)}>&#xe60d;</span>
					<div className="titie"></div>
					
				</header>
				<div id="forget_content">
				<p></p>
					<div className="zhuce">
						<input type="text" id="uerId" placeholder=""/>
						<input type="button" value="" id="btn"/>
					</div>
				</div>
			</div>
			
		)
	}
	
}




